<template>
	<div class="container">
		<h1 class="textcenter">UNIT HOMEWORK</h1>
		<div v-show="loading" class="textcenter">
			<el-button icon="el-icon-loading" 
		            	circle></el-button>
			<h4 class="textcenter">loading...waiting...</h4>
		</div>
		<div class="list">
			<el-input v-model="search" placeholder=""></el-input>
			<div v-for="tes in filteredtests" v-bind:key="tes._id">
			<el-card class="box-card" shadow="hover">
			    <div class="textleft testtitle">
			     <router-link v-bind:to="'/hwk/onehwk/'+tes._id">
			    		{{tes.title}}
			   	 </router-link>
			    </div><el-tag type="info"></el-tag><br/>
			    <div class="textright subtitle">--created by{{tes.author}}</div>
			</el-card>
			</div>
		</div>
		
	</div>
</template>

<script>
import axios from 'axios'
export default{
	name:'test-list',
	data(){
		return{
			hwklist:[],
			search:"",
			loading:true
		}
	},
	created(){
	 // axios.get("http://47.100.60.171:5000/api/hwk/list")
	 axios.get("http://47.100.60.171:5000/api/hwk/list")
		.then(function(data){
			return data.data;
		})
		.then((data)=>{
			if(data.status=="Success"){
				this.$message({
					offset:200,
					message:"",
					type:"success"
				})
				this.hwklist=data.hwklist;
				this.startsort();
				this.loading=false;
			}
			else{
				this.$message({
					message:"",
					type:"error"
				})
			}
		})
	},
	method:{
		sortrule(a,b){ // 
		a=a.title;
		b=b.title;
		return a.localeCompare(b);
    },
    startsort(){
        this.hwklist.sort(this.sortrule);
    },
	},
	computed:{
		filteredtests:function(){
			return this.hwklist.filter((tes)=>{
				return tes.title.match(this.search);
			})
		}
	}
}
</script>

<style scoped>
	.container{
		margin-top:100px;
	}
	
	.list{
		margin-top: 3em;
		margin-bottom: 10em;
		margin-right: 10em;
		margin-left: 10em;
	}
	.textright{
		text-align: right;
	}
	.textleft{
		text-align: left;
	}
	.textcenter{
		text-align: center;
	}
	.testtitle{
		font-size: 16px;
		font-weight: bold;
	}
	.subtitle{
		color: #999999;
		font-size: 10px;
	}
	a{
		text-decoration: none;
		color: #666666;
	}
</style>




