
<template>
	<div class="container">
		<div class="dati">
		<h2 class="textcenter">--{{hwk.title}}--</h2>
		
		<h4 class="textcenter" style="color:grey">
			<el-tag type="info"></el-tag>
		</h4>
		
		<div v-show="loading">
			<el-button icon="el-icon-loading" 
		            	circle></el-button>
			<h4>loading...waiting...</h4>
		</div>
		<div class="test" v-show="!loading">
			<div class="intest">
			<div v-html="hwk.pa1">
				</div>
				<el-divider></el-divider>
			<div v-html="hwk.pa2">
				</div>
			<el-row :gutter="20">
			 <el-col :offset="16">
			 	<el-button v-if="!submitted" type="primary" @click="updateans" round></el-button>
			 </el-col>
			</el-row>
		</div>
		</div>
	</div>
	<div class="daan" v-show="!loading">
			<h2 style="text-align:center;color:#FF4040">----</h2>

			<div class="test" style="border-color:#F08080;background-color:#FFF5EE">
				<el-row :gutter="40"   class="deltext" >
					  <el-col :span="12">
					  	<el-card class="box-card">
					  	<div v-for="(ans,i) in p1ans" >
									{{i+1}}.{{ans}}
						</div>
					  </el-card>
					  </el-col>
					  <el-col :span="12">
					  <el-card class="box-card">
						  <div v-for="(ans,i) in p2ans" >
							{{i+1}}.{{ans}}
						</div>
					  </el-card>
					  </el-col>
			</el-row>
			</div>
	</div>
<!--  -->
 <div class="player">
    <el-row>
    <!--  -->
      <el-col :span="4">
        <!--  -->
        <el-popover placement="top-start" title="" trigger="hover">
	          <div style="text-align: center">
	          		<!--  -->
		            <el-progress type="circle" 
			 	:percentage="music.volume"></el-progress>
		            <br>
		            <!--  -->
		            <el-button icon="el-icon-remove-outline" 
		           	 @click="changeVolume(-10)"
		            	circle></el-button>
		            <el-button icon="el-icon-circle-plus-outline"
		            	@click="changeVolume(10)"
		           	 circle></el-button>
	          </div>
	          <!--  -->
	          <el-button  id="play" slot="reference"
	          :icon="music.isPlay?'el-icon-close':'el-icon-caret-right'" 
	          @click="play"
	          circle></el-button>
        </el-popover>
      </el-col>
      <!--  -->
      <el-col :span="14"  style="padding-left: 0px">
        <el-slider
        	@change="changeTime"
        	:format-tooltip="formatTime"
        	:max="music.maxTime"
        	v-model="music.currentTime" 
        style="width: 100%;"></el-slider>
      </el-col>
      <el-col :span="3" style="padding: 9px 0px 0px 10px;color:#909399;font-size: 13px">
        {{formatTime(music.currentTime)}}/{{formatTime(music.maxTime)}}
      </el-col>
    </el-row>
    <audio ref="music" name="musiclabel" id="musicid" @canplay="getDuration" :src="musicroute"> 
      <!-- <source v-bind:src="musicroute" type="audio/mpeg"> -->
    </audio>
  </div>
  </div>
</template>

<script>
import axios from 'axios'
	export default{

		name:"onehomework",
		data(){
			return{
				id:this.$route.params.id,
				hwk:{},
				p1ans:[],
				p2ans:[],
				loading:true,
				res:[],
				submitted:false,
				seeans:false,
				rcnt:0,
				tot:0,

				//
				changetag:false,
				 musicroute:'',
				        music:{
				          isPlay:false,
				          currentTime:0,
				          maxTime:0,
				          volume:100
				        }
			}
		},
		
		created(){
			axios.get("http://47.100.60.171:5000/api/hwk/list/"+this.id)
					.then(function(data){
						return data.data;
					})
					.then((data)=>{
						if(data.status=="Success"){
							// this.$message({
							// 	offset:200,
							// 	message:"",
							// 	type:"success"
							// });
							this.hwk=data.hwk;
							this.loading=false;
							if(this.hwk.lis!=""&&this.hwk.lis!=null){
								this.musicroute="http://47.96.87.249:8080/dhuf/docu_download?docu_name="+this.hwk.lis;
							}
						}
						else{
							this.$message({
								message:"",
								type:"error"
							})
						}
					})
		},
		mounted(){
		      this.$nextTick(()=>{
		      	this.changetag = !this.changetag
		        setInterval(this.listenMusic,1000)
		      })
		},
		watch:{
			changetag(){
				this.musiclabel.load()
				//alert(this.$refs.music.duration)
			}
		},
		methods:{

			updateans:function(){
				//
				var uans = document.getElementsByTagName('input');
				 this.tot=uans.length;
				// console.log(uans);
				this.p1ans=[]
				this.p2ans=[]
				for(var i=0;i<uans.length;i++){
					if(i<this.hwk.num1)
						this.p1ans[i]=uans[i].value;
					else
						this.p2ans[i-this.hwk.num1]=uans[i].value;
				}
			},
check(){
      
    },


// part
getDuration(){
	//alert('her ')
	//alert(this.$refs.music.duration)
},
listenMusic(){
        if(!this.$refs.music){
          return
        }
        if(this.$refs.music.readyState){
          this.music.maxTime = this.$refs.music.duration
        }
        this.music.isPlay=!this.$refs.music.paused
        this.music.currentTime = this.$refs.music.currentTime
      },
      play(){
        if(this.$refs.music.paused){
          this.$refs.music.play()
        }else{
          this.$refs.music.pause()
        }
        this.music.isPlay=!this.$refs.music.paused
        this.$nextTick(()=>{
          document.getElementById('play').blur()
        })
      },
      changeTime(time){
        this.$refs.music.currentTime = time
      },
      // 
      changeVolume(v){
        this.music.volume += v 
        if(this.music.volume>100){
          this.music.volume = 100
        }
        if(this.music.volume<0){
          this.music.volume = 0
        }
        this.$refs.music.volume = this.music.volume/100
      },
      formatTime(time){
        let it = parseInt(time)
        let m = parseInt(it/60)
        let s = parseInt(it%60)
        return (m<10?"0":"")+parseInt(it/60)+":"+(s<10?"0":"")+parseInt(it%60)
      }
    }  
}
</script>

<style scoped>
	.container{
		margin-top: 100px;
	}
	.textcenter{
		text-align: center;
	}
	.test{
		margin-top: 3em;
		margin-bottom: 10em;
		margin-right: 10em;
		margin-left: 10em;
		text-align: left;
		border-radius: 10px;
		border-width: 3px;
		border-style: solid;
		border-color: #CCCCFF;
		/*background-color:#F0F8FF;*/

	}
	.testres{
		font-size: 16px;
		font-weight: 900;
		color: #F56C6C;
	}
	.cho{
		margin-top: 2em;
	}
	.tihao{
		font-size: 15px;
		margin-bottom: 1em;

	}
	.intest{
		margin: 3em;
	}
	.player{
		width: 500px;
		position: fixed;
		top:80px;
   		 /*text-align: left;*/
   		 border-radius: 20px;
    		 box-shadow: 2px 2px 2px rgba(0,0,0, 0.6);
    		 background-color: #CCCCFF;
	}
</style>


