<template>
	<div class="container">
		<h1 class="textcenter">DELETE LISTENING TESTS</h1>
		<div v-show="loading" class="textcenter">
			<el-button icon="el-icon-loading" 
		            	circle></el-button>
			<h4 class="textcenter">loading...waiting...</h4>
		</div>
		<div class="list">
			<el-input v-model="search" placeholder=""></el-input>
			<div v-for="tes in filteredtests" v-bind:key="tes._id">
			<el-card class="box-card" shadow="hover">
			    <div class="textleft testtitle">
			    <router-link v-if="tes.tag === 'news'" v-bind:to="'/tests/anews/'+tes._id">
			    		{{tes.title}}
			   	 </router-link>
				<router-link v-else v-bind:to="'/tests/atest/'+tes._id">
					{{tes.title}}
			   	 </router-link>
			   <el-tag v-if="tes.tag === 'level4' " type="success">cet4</el-tag>
			    <el-tag v-else-if="tes.tag === 'level6' " type="danger">cet6</el-tag>
			    <el-tag v-else-if="tes.tag === 'news' " type="warning">news</el-tag>
			    <el-tag v-else type="info">oth</el-tag>
				<el-button type="danger" size="small" icon="el-icon-delete" @click="deleteById(tes._id)" round></el-button>
			    </div>
			    <div class="textright subtitle">
					--created by{{tes.author}}</div>
			</el-card>
			</div>
		</div>
		
	</div>
</template>

<script>
import axios from 'axios'
export default{
	name:'test-list',
	data(){
		return{
			tests:[],
			search:"",
			loading:true
		}
	},
	created(){
	 //axios.get("http://localhost:5000/api/listes/list")
		axios.get("http://47.100.60.171:5000/api/listes/list")
		.then(function(data){
			return data.data;
		})
		.then((data)=>{
			console.log(data);
			if(data.status=="Success"){
				this.$message({
					message:"",
					type:"success"
				})
				this.tests=data.tests;
				this.loading=false;
			}
			else{
				this.$message({
					message:"",
					type:"error"
				})
			}
		})
	},
	methods:{
		deleteById(id){
			this.$confirm(', ?', '', {
				confirmButtonText: '',
				cancelButtonText: '',
				type: 'warning'
				})
				.then(() => {
					//axios.get("http://localhost:5000/api/listes/del/"+id)
					axios.get("http://47.100.60.171:5000/api/listes/del/"+id)
					.then(function(data){
						return data.data;
					})
					.then((data)=>{
						console.log(data);
						if(data.status=="Success"){
							this.$message({
								offset:200,
								message:"",
								type:"success"
							});
						}
						else{
							this.$message({
								offset:200,
								message:"",
								type:"error"
							})
						}
						})
				})
				.catch(() => {
				this.$message({
					type: 'info',
					message: ''
				});          
				});
		}
	},
	computed:{
		filteredtests:function(){
			return this.tests.filter((tes)=>{
				return tes.title.match(this.search);
			})
		}
	}
}
</script>

<style scoped>
	.container{
		margin-top:100px;
	}
	
	.list{
		margin-top: 3em;
		margin-bottom: 10em;
		margin-right: 10em;
		margin-left: 10em;
	}
	.textright{
		text-align: right;
	}
	.textleft{
		text-align: left;
	}
	.textcenter{
		text-align: center;
	}
	.testtitle{
		font-size: 16px;
		font-weight: bold;
	}
	.subtitle{
		color: #999999;
		font-size: 10px;
	}
	a{
		text-decoration: none;
		color: #666666;
	}
</style>




