<template>
	<div class="container">
		<el-tag effect="dark" type="info"></el-tag>
	<div v-if="!submitted">
	<!--  -->
		<el-steps :active="nowstep" align-center>
		  <el-step title=" 1" description=""></el-step>
		  <el-step title=" 2" description=""></el-step>
		  <el-step title=" 3" description=""></el-step>
		  <el-step title=" 4" description=""></el-step>
		  <el-step title=" 5" description=""></el-step>
		</el-steps>
		<el-button style="margin-top: 12px;"  type="primary" plain @click="laststep"></el-button>
		<el-button style="margin-top: 12px;" @click="nextstep"></el-button>

	<!--  -->
	<el-row :gutter="40"  v-show="nowstep == 0" class="deltext" >
	<div class="subtitle">
		<strong> 1</strong><br>
		<div class="smaller">
			
		</div>
		<el-divider></el-divider>
	</div>
	  <el-col :span="12">
	  	<el-input
		  type="textarea"
		  :autosize="{ minRows: 5}"
		  placeholder=""
		  v-model="words">
		</el-input>
		 <el-button v-on:click="getword"></el-button>
	  </el-col>
	  <el-col :span="12">
	  <el-card class="box-card">
		  <strong v-if="tes.words.length>0" ></strong>
	  	<div class="qq" v-for="(qq,index) in tes.words" v-bind:key="index">
	  		<strong>{{index+1}}:</strong><br>
	  		&nbsp;&nbsp;{{qq}}<br>
	  	</div>
	  </el-card>
	  </el-col>
	</el-row>

	<!--  -->
	<el-row :gutter="40"  v-show="nowstep == 1" class="deltext" >
	<div class="subtitle">
		<strong> 2</strong><br>
		<div class="smaller">
			
		</div>
		<el-divider></el-divider>
	</div>
	  <el-col :span="12">
	  	<el-input
		  type="textarea"
		  :autosize="{ minRows: 5}"
		  placeholder=""
		  v-model="textarea">
		</el-input>
		 <el-button v-on:click="getques"></el-button>
	  </el-col>
	  <el-col :span="12">
	  <el-card class="box-card">
		  <strong v-if="tes.ques.length>0" ></strong>
	  	<div class="qq" v-for="(qq,index) in tes.ques" v-bind:key="index">
	  		<strong>{{index+1}}:</strong><br>
	  		&nbsp;&nbsp;{{qq}}<br>
	  	</div>
	  </el-card>
	  </el-col>
	</el-row>

	<!--  -->
	<el-row :gutter="40"  v-show="nowstep == 2" class="deltext" >
	<div class="subtitle">
		<strong> 3</strong><br>
		<div class="smaller">
			
		</div>
		<el-divider></el-divider>
	</div>
	  <el-col :span="12">
	  	<el-input
		  type="textarea"
		  :autosize="{ minRows: 5}"
		  placeholder=""
		  v-model="textarea2">
		</el-input>
		 <el-button v-on:click="getans"></el-button>
	  </el-col>
	  <el-col :span="12">
	  <el-card class="box-card">
	  	<div class="qq" v-for="(aa,index) in tes.ans" v-bind:key="index">
	  		<strong>Q{{index+1}}</strong>
	  		&nbsp;{{aa}}<br>
	  	</div>
	  </el-card>
	  </el-col>
	</el-row>

	<!--  -->
	<el-row :gutter="40"  v-show="nowstep == 3" class="deltext" >
	<div class="subtitle">
		<strong> 4</strong><br>
		<div class="smaller">
			html<br>
			&amp;nbsp;<br> 
			<br>
			: strong<br>&lt;strong&gt; title&lt;/strong&gt;=><strong>title</strong><br/>
		</div>
		<el-divider></el-divider>
	</div>
	  <el-col :span="12">
	  	<el-input
		  type="textarea"
		  :autosize="{ minRows: 5}"
		  placeholder=""
		  v-model="textarea3">
		</el-input>
		 <el-button v-on:click="getorg"></el-button>
	  </el-col>
	  <el-col :span="12">
	  <el-card class="box-card">
	  <div class="qq">
	  	<div class="ok" v-html="tes.org"></div>
	  </div>
	
	  </el-card>
	  </el-col>
	</el-row>

	<!-- mp3 -->
	<el-row :gutter="40"  v-show="nowstep == 4" class="deltext" >
	<div class="subtitle">
		<strong> 4mp3</strong><br>
		<div class="smaller">
		</div>
		<el-divider></el-divider>
		<!-- part -->
		<el-upload
			  class="upload-file"
			  drag
			  action="http://47.96.87.249:8080/dhuf/docu_upload"
			  :on-success="uploadSuccess"
			 >
			  <i class="el-icon-upload"></i>
			  <div class="el-upload__text">
			  	<em></em>
			  </div>
		</el-upload>
	</div>
	</el-row>

	<!--  -->
	<el-row :gutter="40"  v-show="nowstep == 5" class="deltext" >
	<div class="subtitle" style="font-size:16px;font-weight:300">
		<el-row :gutter="20">
			<el-col :span="8">
			</el-col>
			<el-col :span="12">
			<el-input v-model="tes.title"/>
		</el-col>
		</el-row>
		<el-row :gutter="20" class="deltext">
			<el-col :span="8">
			</el-col>
			<el-col :span="12">
			<el-input v-model="tes.author" />
			</el-col>
		</el-row>
		<div  class="deltext">
		</div>
	</div>
	</el-row>
	</div>

	<div v-if="submitted" class="successtip">
		<h2> successful!</h2>
		      <img src="../../assets/success.jpg" class="roundimg">
		      <div style="padding: 14px;">
		        <div class="bottom clearfix">
		          <time class="time">{{ currentDate }}</time>
		        </div>
		      </div>
	</div>
	</div>
</template>
<script>
	import axios from 'axios'
	export default{
		data(){
			return{
				tes:{
					title:'',
					author:'',
					ques:[],
					ans:[],
					org:'',
					lis:'',
					words:[]
				},
				currentDate: new Date(),
				words:'',
				textarea:'',
				textarea2:'',
				textarea3:'',
				nowstep:0,
				submitted:false
			}
		},
		methods:{
			getword:function(){
				this.tes.words=[];
				var tmp=this.words;
				var pos=tmp.indexOf("\n");
				var nowq;
				while(pos!=-1&&tmp!=''){
					var ww=tmp.slice(0,pos);
					tmp=tmp.slice(pos+1);
					pos=tmp.indexOf("\n");
					if(ww.length>0)
						this.tes.words.push(ww);
				}
				if(tmp!='')
					this.tes.words.push(tmp);
			},
			getques:function(){
				this.tes.ques=[];
				var tmp=this.textarea;
				var pos=tmp.indexOf("\n");
				while(pos!=-1&&tmp!=''){
					var ww=tmp.slice(0,pos);
					tmp=tmp.slice(pos+1);
					pos=tmp.indexOf("\n");
					if(ww.length>0)
						this.tes.ques.push(ww);
				}
				if(tmp!='')
					this.tes.ques.push(tmp);
			},
			getans:function(){
				this.tes.ans=[];
				var tmp=this.textarea2;
				var pos=tmp.indexOf("\n");
				while(pos!=-1&&tmp!=''){
					var ww=tmp.slice(0,pos);
					tmp=tmp.slice(pos+1);
					pos=tmp.indexOf("\n");
					if(ww.length>0)
						this.tes.ans.push(ww);
				}
				if(tmp!='')
					this.tes.ans.push(tmp);
			},
			getorg:function(){
				this.tes.org=this.textarea3.replace(/\n/g,"<br>");
				// this.tes.org=this.tes.org.replace(/\s/g,"&nbsp;");
				console.log(this.tes.org);
			},
			//url
			uploadSuccess(response,file,fileList){
				alert("mp3 upload success")



var msg = response;
      var obj = JSON.parse(msg[0]);
      console.log(obj.status);
      if (obj.status == "success") {
        this.$message({
          message: "",
          type: "success"
        });
      } else if (obj.status == "exist") {
        this.$message({
          message: "",
          type: "warning"
        });
      } else if (obj.status == "nofile") {
        this.$message({
          message: "",
          type: "warning"
        });
      } else {
        this.$message.error("");
      }
				this.tes.lis=file.name;
				// console.log(this.tes);
			},
			post:function(){
				console.log(this.tes);
				axios.post("http://47.100.60.171:5000/api/listes/addnews",this.tes)
				.then((data)=>{
					//console.log(data);
					this.submitted=true;
					var msg=data.data;
					var status=JSON.parse(msg[0]).status;
					if(status=="Success"){
						this.$message({
							message:"",
							type:"success"
						})
					}else {
						this.$message({
							message:"",
							type:"error"
						})
					}
				});
			},
			// 
			nextstep(){
				if(this.nowstep<5)
					this.nowstep+=1;
				else
					this.post();
			},
			//
			laststep(){
				if(this.nowstep>0)
				this.nowstep-=1;
			}
		}
	}
</script>
<style scoped>
	.container{
		text-align: center;
		margin-top: 90px;
		padding: 1em 6em 4em 6em;
	}
	.box-card .qq{
		text-align: left;
		max-width: 100%
	}
	.ok{
		width: 100%;
		margin: 1rem;
	}
	.deltext{
		padding-top: 2em
	}
	.el-button{
		margin-top: 1em;
	}
	.subtitle{
		text-align: center;
		font-size: 20px;
	}
	.smaller{
		margin-top: 0.5em;
		font-size: 16px;
	}
	.roundimg{
		border-radius: 50%;
		max-width: 200px;
		border: 10px solid #FFCC99;
	}
</style>


