<template>
	<div class="container">
		<el-tag effect="dark" type="info"></el-tag>
		<div v-if="!submitted">
			<el-steps :active="nowstep" align-center class="textcenter" >
		  <el-step title=" 11" ></el-step>
		  <el-step title=" 22" ></el-step>
		  <el-step title=" 3" ></el-step>
		</el-steps>
		<el-button style="margin-top: 12px;" class="textcenter"  type="primary" plain @click="laststep"></el-button>
		<el-button style="margin-top: 12px;" class="textcenter"  @click="nextstep"></el-button>
		

		<!-- 1. -->
			<el-row :gutter="40"  v-show="nowstep == 0" class="deltext" >
			<div class="subtitle">
				<strong>1</strong><br>
				<div class="smaller">
					1).<br>
					2).(__)<br>
					3).######<br>
					4).<br>
				</div>
				<el-divider></el-divider>
			</div>

			  <el-col :span="12">
			  	<el-input
				  type="textarea"
				  :autosize="{ minRows: 5}"
				  placeholder=""
				  v-model="textarea1">
				</el-input>
				<el-button type="primary" v-on:click="quicktag"></el-button>
				 <el-button v-on:click="getpa1"></el-button>
			  </el-col>
			  <el-col :span="12">
			  <el-card class="box-card">
			  	<div class=textcenter>-----{{hwk.num1}}-----</div>
			  	<div v-html="hwk.pa1" class="textleft"></div>
			  </el-card>
			  </el-col>
			</el-row>

		<!-- 2. -->
			<el-row :gutter="40"  v-show="nowstep == 1" class="deltext" >
			<div class="subtitle">
				<strong>2</strong><br>
				<div class="smaller">
					1).<br>
					2).(__)<br>
					3).######<br>
					4).<br>
				</div>
				<el-divider></el-divider>
			</div>

			  <el-col :span="12">
			  	<el-input
				  type="textarea"
				  :autosize="{ minRows: 5}"
				  placeholder=""
				  v-model="textarea2">
				</el-input>
				<el-button type="primary" v-on:click="quicktag2"></el-button>
				 <el-button v-on:click="getpa2"></el-button>
			  </el-col>
			  <el-col :span="12">
			  <el-card class="box-card">
			  	<div class=textcenter>-----{{hwk.num2}}-----</div>
			  	<div v-html="hwk.pa2" class="textleft"></div>
			  </el-card>
			  </el-col>
			</el-row>
	

		<!-- 3.mp3 -->
			<el-row :gutter="40"  v-show="nowstep == 2" class="deltext" >
			<div class="subtitle">
				<strong> 4mp3</strong><br>
				<div class="smaller">
				</div>
				<el-divider></el-divider>
				<!-- part -->
				<el-upload
					  class="upload-file"
					  drag
					  action="http://47.96.87.249:8080/dhuf/docu_upload"
					  :on-success="uploadSuccess"
					 >
					  <i class="el-icon-upload"></i>
					  <div class="el-upload__text">
					  	<em></em>
					  </div>
				</el-upload>
			</div>
			</el-row>

	<!--  -->
	<el-row :gutter="40"  v-show="nowstep == 3" class="deltext" >
	<el-col >
	<div class="subtitle" style="font-size:16px;font-weight:300">
		<el-row :gutter="20">
			<el-col :span="8">
			</el-col>
			<el-col :span="12">
			<el-input v-model="hwk.title"/>
		</el-col>
		</el-row>
		<el-row :gutter="20" class="deltext">
			<el-col :span="8">
			</el-col>
			<el-col :span="12">
			<el-input v-model="hwk.author" />
			</el-col>
		</el-row>
		<div  class="deltext">
		</div>
	</div></el-col>
	</el-row>

	</div>

		<div v-if="submitted" class="successtip">
		<h2> successful!</h2>
		      <img src="../../assets/success.jpg" class="roundimg">
		      <div style="padding: 14px;">
		        <div class="bottom clearfix">
		          <time class="time">{{ nowDate }}</time>
		        </div>
		      </div>
	</div>
	</div>
</template>
<script>
	import axios from 'axios'
	export default{
		data(){
			return{
				hwk:{
					pa1:'',
					num1:0,
					pa2:'',
					num2:0,
					title:'',
					author:''
				},
				raw:'',
				currentDate: new Date(),
				textarea1:'',
				textarea2:'',
				nowstep:0,
				submitted:false
			}
		},
		methods:{
			quicktag:function(){
				var reg=/_+[0-9]*_+/g;
				var cnt=1;
				// alert('here')
				// this.raw=this.textarea.replace(reg,"");
				this.textarea1=this.textarea1.replace(reg,"######");
				this.textarea1 = this.textarea1.replace(/\n/g,"<br>");
			},
			quicktag2:function(){
				var reg=/_+[0-9]*_+/g;
				var cnt=1;
				// alert('here')
				// this.raw=this.textarea.replace(reg,"");
				this.textarea2=this.textarea2.replace(reg,"######");
				this.textarea2 = this.textarea2.replace(/\n/g,"<br>");
			},
			getpa1:function(){
				var tmp = this.textarea1;
				var input = `
					<input type="text" style="width:80px;border-bottem:1px solid #dbdbdb;border-top:0px;border-left:0px;border-right:0px;outline:none;"></input>
					`;
				var pos = tmp.indexOf("######");
				var id = 1;
				while(pos!=-1){
					tmp = tmp.replace("######","("+id+")."+input);
					var pos = tmp.indexOf("######");
					id++;
				}
				this.hwk.pa1 = tmp
				this.hwk.num1 = id-1
			},
			getpa2:function(){
				var tmp = this.textarea2;
				var input = `
					<input type="text" style="width:80px;border-bottem:1px solid #dbdbdb;border-top:0px;border-left:0px;border-right:0px;outline:none;"></input>
					`;
				var pos = tmp.indexOf("######");
				var id = 1;
				while(pos!=-1){
					tmp = tmp.replace("######","("+id+")."+input);
					var pos = tmp.indexOf("######");
					id++;
				}
				this.hwk.pa2 = tmp
				this.hwk.num2 = id-1
			},
			//url
			uploadSuccess(response,file,fileList){
				alert("mp3 upload success")
				var msg = response;
				      var obj = JSON.parse(msg[0]);
				      if (obj.status == "success") {
				        this.$message({
				          message: "",
				          type: "success"
				        });
				      } else if (obj.status == "exist") {
				        this.$message({
				          message: "",
				          type: "warning"
				        });
				      } else if (obj.status == "nofile") {
				        this.$message({
				          message: "",
				          type: "warning"
				        });
				      } else {
				        this.$message.error("");
				      }
				this.hwk.lis=file.name;
			},
			post:function(){
				axios.post("http://47.100.60.171:5000/api/hwk/add",this.hwk)
				.then((data)=>{
					var msg=data.data;
					if(msg.status=="Success"){
						this.$message({
							message:"",
							type:"success"
						})
						this.submitted=true;
					}else {
						this.$message({
							message:"",
							type:"error"
						})
						alert('something wrong')
					}
				});
			},
			// 
			nextstep(){
				if(this.nowstep<=2)
					this.nowstep+=1
				else
					this.post()
			},
			//
			laststep(){
				if(this.nowstep>0)
				this.nowstep-=1;
			}
			
		},
		computed:{
			nowDate(){
				this.currentDate=new Date();
				return this.currentDate;
			}
		}
	}
</script>
<style scoped>
	.container{
		text-align: center;
		margin-top: 90px;
		padding: 1em 6em 4em 6em;
	}
	.textcenter{
		text-align: center;
	}
	.box-card .qq{
		text-align: left;
		max-width: 100%
	}
	.ok{
		width: 100%;
		margin: 1rem;
	}
	.deltext{
		padding-top: 2em
	}
	.el-button{
		margin-top: 1em;
	}
	.subtitle{
		text-align: center;
		font-size: 20px;
	}
	.smaller{
		margin-top: 0.5em;
		font-size: 16px;
	}
	.roundimg{
		border-radius: 50%;
		max-width: 200px;
		border: 10px solid #FFCC99;
	}
	.textleft{
		text-align: left;
	}
</style>


